import React, { useEffect, useState } from "react";
import { withRouter } from "react-router";
import styled from "styled-components";
import Loader from "../../../Components/Loader";

const Container = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(22%, 1fr));
  /* grid-auto-flow: row dense; */
  gap: 15px;
`;
const ItemContainer = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: flex-end;

  background-color: rgba(150, 150, 150, 0.25);
  border-radius: 10px;
  padding: 10px;
  padding-bottom: 10px;
`;

const PosterContainer = styled.div`
  /* aspect-ratio: 1/1.2; */
  height: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
  object-fit: contain;
`;

const Poster = styled.img`
  min-width: 60px;
  width: 100%;
  object-fit: contain;
`;

const Name = styled.div`
  font-size: 10px;
  opacity: 0.7;
  margin-top: 10px;
`;

const Airing = styled.div`
 font-size: 10px;
 opacity: 0.7;
 margin-top:2px;
`

const DetailSeasons = withRouter((props) => {
  const [loading, setLoading] = useState(true);
  const { seasons } = props.props;

  useEffect(() => {
    console.log("detailSeasons:", seasons);
    setLoading(false);
  }, []);

  return loading ? (
    <Loader />
  ) : seasons.length === 0 ? (
    "No season data available..."
  ) : (
    <Container>
      {seasons.map((season) => (
        <ItemContainer>
          <PosterContainer>
            <Poster
              src={
                season.poster_path
                  ? `https://image.tmdb.org/t/p/w200${season.poster_path}`
                  : require("../../../assets/noimagetrans.png").default
              }
            />
          </PosterContainer>
          <Name>{season.name}</Name>
          <Airing>{season.air_date && season.air_date.substr(0,4)}</Airing>
        </ItemContainer>
      ))}
    </Container>
  );
});

export default DetailSeasons;
